.acceptables {
    height: 95vh;
    width:20%;
    overflow-y: auto;
}
.date {
    display: flex;
}
.dateStatus {
    font-size: xx-large;
    
}
.dateContainer {
    display: flex;
    margin-left: 4px;
    flex-direction: column;
    width: 100%;
}
.langName {
    text-align: center;
    font-size: large;
    color: #002140;
    border-radius: 10px;
    text-align: center;
    font-weight: bolder;
}
.dateLocation {
    text-transform: capitalize;
    font-weight: bolder;
}
.dateTime {
    font-size: 5px;
}
.container {
    display: flex;
}