
.mapContainer {
    margin: auto;
    display: flex;
    width: 80%;
    height: 73%;
    margin-top: 5%;
    margin-bottom: 7%;
    border-radius: 15px;
    border: 1px solid #1677ff;

    .chatContex {
        width: 100%;
        height: 100%;
        .selected {
            background-color: #1677ff;
            color: white;
        }
        .chatHeader {
            border-radius: 0px 15px 0px 0px;
            height: 10%;
            border: 3px solid #1677ff;
            display: flex;
            flex-direction: row;
            margin-bottom: 10px;
            .userDetails {
                margin-left: 20px;
                width: 70%;
                display: flex;
                flex-direction: row;
                justify-content: left;
                align-items: center;
                .avatar {
                    width: 50px;
                    height: 50px;
                    background-color: white;
                    border: 4px solid blue;
                }
                .labels {
                    margin-left: 10px;
                    display: flex;
                    flex-direction: column;
                    .title {
                        text-transform: uppercase;
                        font-size: 15px;
                        font-weight: bolder;
                    }
                    .location {
                        font-weight: 100;
                    }
                }
            }
            .buttons {
                width: 30%;
                display: flex;
                justify-content: center;
                align-items: center;
            }
        }
        .chatMessages {
            height: 77%;
            overflow-y: auto;
            display: flex;
            flex-direction: column;
            gap: 10px;
            margin-bottom: 10px;
            .messageLine {
                width: 100%;
                border-top: 10px;
                display: flex;
                .messageContent {
                    min-width: 30%;
                    max-width: 80%;
                    min-height: 50px;
                    background-color: rgb(239, 239, 239);
                    color: black;
                    display: flex;
                    flex-direction: column;
                    text-align: end;
                    border-radius: 15px;
                    padding: 10px;
                    margin-left: 10px;
                    .message {
                        width: 100%;
                        font-size: 14px;
                        min-height: 20px;
                        text-align: left;
                    }
                    .time {
                        width: 100%;
                        align-items: end;
                        font-size: 8px;
                        padding-right: 5px;
                    }
                }
            }
        }
        .chatSender {
            height: 10%;
            border-top: 1px solid #1677ff;
            padding: 10px;
            display: flex;
            flex-direction: row;
            gap: 10px;
            
            .input {
                width: 90%;
            }
            .sendBtn {
                height: 100%;
                border-radius: 50%;
            }
        }
    }
}
.userContex {
    height: 100%;
    width: 25%;
    border-right: 1px solid gray;
    padding: 15px;
    overflow-y: auto;

    .searchBar {
        height: 10%;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
    }
}
.chatBox {
    width: 75%;
}


.myLine {
    justify-content: end;
    .messageContent {
        background-color: #1677ff !important;
        color: white !important;
        margin-right: 10px;
        margin-left: unset;
        .message {
            text-align: end !important;
        }
        .time {
            text-align: left;
            align-items: start !important;
            padding-right: unset !important;
            padding-left: 5px !important;
        }
    }
}
.me {
    border-style: dashed !important;
}
.notAccepted {
    border-color: #1677ff !important;
    border-width: 3px !important;
    border-radius: 0px !important;
}
.userCompContainer {
    width: 100%;
    border: 1px solid gray;
    border-radius: 10px;
    padding: 10px;
    display: flex;
    justify-content: center;
    margin-bottom: 5px;
    .avatar {
        width: 45px;
        height: 45px;
        border-radius: 50%;
        margin: auto;
        margin-left: 5px;
        margin-right: 5px;
    }
    .content {
        margin: auto;
        display: flex;
        flex-direction: column;
        height: 100%;
        justify-content: center;
        .title {
            font-weight: bolder;
            font-size: 15px;
        }
        .context {
            font-size: 12px;
        }

    }
}